export class ParametricItem {
  get id(): number {
    return this._id;
  }

  set id(id: number) {
    this._id = id;
  }

  get name(): string {
    return this._name;
  }

  set name(name: string) {
    this._name = name;
  }

  private _id: number;

  private _name: string;

  static parse(json: any[]): ParametricItem[] {
    return json.map((item: any): ParametricItem => new ParametricItem(item));
  }

  constructor(json?: any) {
    if (json) {
      this._id = json.codTablaParametrica;
      this._name = json.nombreParametro;
    }
  }
}
